@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

*, *::before, *::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.App{
  min-height: 100vh;
}

.center{
  text-align: center;
}


.p-top{
  padding-top: 35px;
}

.heading{
  font-size: 35px;
  font-weight: bold;
}

.sub-heading{
  font-size: 15px;
  font-weight: 400;
}